#table {
  margin-right: 5%;;
  margin-left: 5%;
  margin-bottom: 5vh;
  box-sizing: border-box;
  max-width: 90%;
}

#table button {
  color: #222C35;
}

#table h6 {
  font-weight: 700;
  text-transform: uppercase;
  color: #222C35;
}

#table th {
  text-transform: uppercase;
  -webkit-transition: 0.2s ease;
  transition: 0.2s ease;
}

#table button:hover,
#table th:hover {
  color: #D72322;
}

.Leaderboards {
  min-height: 94.9vh;
  position: relative;
}

