  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.date {
  margin: 3vh 10vw !important;
}

@media only screen and (min-width: 960px) {
  #table th,
  #table td {
    padding-left:10px;
    padding-right: 10px;
  }
}
.Events {
  min-height: 94.9vh;
  position: relative;
}
.navLink a {
  text-decoration: none;
  color: #000;
}

.navLink > a:hover,
.navLink > a.active {
  color: #D72322;
}
.Tabs {
  display: -webkit-flex;
  display: flex; /** Use CSS flexbox for easy responsive stuff */
  position: relative;
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 5vh;
  color: white;
  background-color: #222C35;
  border-bottom: 1px solid black;
  z-index: 1000; /** Make sure that it's always on top of everything */
}

.tabs-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start; /** Content automatically sticks to the left side of the container */
  -webkit-align-items: center;
          align-items: center; /** Align items vertically to center so they appear in the middle of the navigation bar */
  width: 50%;
}

.action-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end; /** Content automatically sticks to the right side of the container */
  -webkit-align-items: center;
          align-items: center; /** Align items vertically to center so they appear in the middle of the navigation bar */
  width: 50%;
}
.NavItem {
  margin: 0 5vw;
  padding: 0;
}

.NavItem > a {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  text-decoration: none;
  color: #fff;
  -webkit-transition: 0.2s ease;
  transition: 0.2s ease;
}

.NavItem > a > p {
  margin-left: 3px;
  display: none;
}

.NavItem > a.active,
.NavItem > a:hover {
  color: #D72322;
}

.active .item:after {
  left: 0;       
  width: 100%; 
}

.item:after {
  content: "";
  display: block;
  height: 2px;
  left: 50%;
  position: relative;
  background: #D72322;
  -webkit-transition: width 0.3s ease 0s, left 0.3s ease 0s;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;      
}


@media only screen and (min-width: 768px) {
  .NavItem > a > p {
    display: inline-block;
  }

  .linkWrapper:hover .item:after {
    width: 100%; 
    left: 0;       
  }
}





